/* src/App.css */
.App { text-align: center; }
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
input[type="text"] {
  padding: 10px;
  font-size: 16px;
  margin: 10px 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
}
button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  background-color: #61dafb;
}
.chat-room {
  border: 1px solid #61dafb;
  border-radius: 8px;
  padding: 20px;
  width: 80%;
  max-width: 600px;
}
.messages {
  height: 400px;
  overflow-y: auto;
  background-color: #444a54;
  padding: 10px;
  text-align: left;
  border-radius: 5px;
  margin-bottom: 10px;
}
.user-message { margin-bottom: 5px; }
.system-message {
  margin-bottom: 5px;
  font-style: italic;
  color: #aaa;
}
.typing-indicator {
  height: 20px;
  font-size: 14px;
  font-style: italic;
  color: #aaa;
}
.message-input { display: flex; }
.message-input input { flex-grow: 1; }